.image-gallery-thumbnail {
    width: 120px
}

.image-gallery-slide-wrapper {
    box-shadow: 3px 3px 10px 0px black;
    margin-bottom: 1rem
}

.image-gallery-thumbnail img{
    height: 60px;
    width: auto;
    box-shadow: 1px 1px 4px black
}
.modal {
    font-size: 18px;
  }

.modal > .header {
    width: 100%;
    border-bottom: 1px solid gray;
    font-size: 26px;
    text-align: center;
    padding: 5px;
  }

.modal > .content {
    width: 90%;
    padding: 10px 5px;
    text-align: justify;
  }

.modal > .actions {
    width: 100%;
    padding: 10px 5px;
  }

.modal > .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 18px;
    border: 1px solid #cfcece;
  }

  
p > button {
    padding: 0;
    border: none;
    background: none;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

